body{
  background-color: #eee;
  padding: 2rem;
}
button{
  width: 1.5rem;
  height: 1.5rem;
  border-radius: 100%;
  font-size: 1.2rem;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: transparent;
  outline: none;
  border: 1px solid #000;
  transition: .3s;
  cursor: pointer;
}
button:hover{
  background-color: #000;
  color: #fff;
}
.wrap{
  width: 90%;
  margin: auto;
  display: flex;
  justify-content: space-between;
  gap: 1rem;
} 

.item {
  width: 25%;
  padding: 1rem 0.8rem 2rem;
  background-color: #fff;
  border-radius: 10px;
}

.item div{
  align-items: center;
  display: flex;
  gap: 1rem;
}